.themes-block {
  @include media('>=desktop') {
    margin-bottom: 45px;
  }
  h2, .h2 {
    color: $black;
  }
  img {
    width: 100%;
    margin-bottom: 20px;
    vertical-align: top;
    @include media('>=tablet') {
      margin-bottom: 0;
    }
  }
  .themes-box {
    padding-bottom: 20px;
    margin-bottom: 25px;
    position: relative;
    @include media('>=desktop') {
      padding-bottom: 40px;
      margin-bottom: 50px;
    }
    &:after {
      content: '';
      position: absolute;
      left: 15px;
      right: 15px;
      bottom: 0;
      display: block;
      height: 2px;
      background-color: #f7f7f7;
    }
    h2, .h2 {
      @include media('>=tablet') {
        margin-top: -6px;
        margin-bottom: 30px;
      }
    }
    p {
      max-width: 380px;
      color: rgba($secondary-color, .8);
    }
  }
}