

.info-slides {
    position: relative;
    z-index: 3;
    padding: 0 0 50px;
    @include media(">=small-desktop") {
        padding: 0;
        height: 100%;
        transition: opacity 0.5s;
        opacity: 0;
    }
    &.slick-initialized {
        @include media(">=small-desktop") {
            opacity: 1;
        }
    }
    .slick-list,
    .slick-track,
    .slick-slide>div {
        height: 100%;
    }
    .slide {
        position: relative;
        @include media(">=small-desktop") {
            vertical-align: top;
            height: 100%;
        }
    }
    .slick-slide {
        @include media(">=small-desktop") {
            height: 100vh;
        }
        >* {
            opacity: 0;
            transition: opacity 0.5s;
        }
        &.slick-current {
            >* {
                opacity: 1;
            }
        }
    }
}
.info-slides-line {
    display: none;
    position: absolute;
    z-index: 5;
    bottom: 0;
    left: 0;
    @include size(0, 10px);
    background: $white;
    transition: width 0.5s;
    @include media(">=small-desktop") {
        display: block;
    }
}

.link-next {
    position: absolute;
    z-index: 4;
    top: calc(100vh - 120px);
    right: 35px;
    line-height: 20px;
    font-size: 14px;
    font-weight: bold;
    transform: rotate(90deg);
    transform-origin: 100% 100%;
    transition: all 0.25s;
    @include media(">=small-desktop") {
        position: fixed;
        top: auto;
        right: 50px;
        bottom: 24px;
        overflow: hidden;
        width: 60px;
        transform: rotate(0);
        &:hover {
            width: 90px;
        }
    }
    a {
        display: block;
        width: 90px;
        padding: 0 34px 0 0;
        text-decoration: none;
        background: url('../images/icon-arrow-right-white.svg') no-repeat 100% 50%;
        transition: all 0.25s;
        @include media("<small-desktop") {
            opacity: 1 !important;
            pointer-events: all !important;
        }
        &.disabled {
            opacity: 0;
            pointer-events: none;
        }
    }
}

#canvas {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.dg.ac {
    display: none !important;
}