html {
    height: auto;
    @include media(">=small-desktop") {
        height: 100%;
    }
}

body {
    &.inverse {
        height: auto;
        @include media(">=small-desktop") {
            height: 100%;
        }
    }
}
.main {
    flex-shrink: 0;
    flex-grow: 1;
    margin: 0 auto;
    max-width: 1200px;
    padding: 0 15px;
    width: 100%;
    .inverse & {
        max-width: inherit;
        padding: 0;
        height: auto;
        @include media(">=small-desktop") {
            height: 100%;
        }
    }
}

.wrapper {
    position: relative;
    overflow-x: hidden;
    width: 100%;
    @include flex(column);
    flex-shrink: 0;
    flex-grow: 1;
    min-height: 100vh;
    height: 100%;
    .inverse & {
        color: $white;
        min-height: 0;
        height: auto;
        background: $primary-color;
        @include media(">=small-desktop") {
            height: 100%;
        }
    }
}

.header,
.footer {
    flex-shrink: 0;
}




.arrows-nav {
    margin: 0 0 70px;
    text-align: center;
    @include media("<1025px") {
        display: none;
    }
    a {
        display: inline-block;
        margin: 0 3px;
        text-decoration: none;
        vertical-align: top;
        color: rgba($primary-color, 1);
        transition: color 0.25s;
        &.slick-disabled {
            pointer-events: none;
            color: rgba($primary-color, 0.2);
        }
        i {
            display: block;
        }
    }
}