.partners-block {
    margin: 0 0 40px;
    h2 {
        margin: 0 0 20px;
        line-height: 30px;
        font-size: 24px;
        font-weight: 700;
        color: $secondary-color;
        @include media(">=small-desktop") {
            margin: 0 0 40px;
            line-height: 50px;
            font-size: 40px;
        }
    }
}

.partners-grid {
    margin: 0 auto 40px;
    max-width: 1145px;
    line-height: 0;
    font-size: 0;
    .partner {
        display: inline-block;
        vertical-align: top;
        position: relative;
        // margin: 0 -1px 0 0;
        // padding-top: 50%;
        width: 100%;
        height: 120px;
        // width: calc(50% + 1px);
        @include media(">=480px") {
            // padding-top: 33%;
            // width: calc(33% + 1px);
            width: 50%;
        }
        @include media(">=767px") {
            // padding-top: 25%;
            // width: calc(25% + 1px);
            width: 33%;
        }
        @include media(">=small-desktop") {
            // padding-top: 10%;
            // width: calc(20% + 1px);
        }
        .border {
            position: absolute;
        }
        .top,
        .bottom {
            top: 0;
            left: 0;
            right: 0;
            height: 1px;
            background: url("../images/black-dots.png");
        } 
        .bottom {
            top: auto;
            bottom: 0;
        }
        .left,
        .right {
            top: 0;
            left: 0;
            bottom: 0;
            width: 1px;
            background: url("../images/black-dots.png");
        }
        .right {
            left: auto;
            right: 0;
        }
        .name {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            // transition: opacity 0.25s;
            span {
                display: block;
                text-align: center;
                line-height: 58px;
                font-size: 60px;
                font-weight: 700;
                color: $primary-color;
            }
            strong {
                display: block;
                line-height: 14px;
                font-size: 14px;
                font-weight: 400;
                text-align: center;
                color: rgba($black, 0.2)
            }
        }
        .logo {
            // transition: opacity 0.25s;
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                max-width: 80%;
                max-height: 80%;
                opacity: 0;
            }
        }
        &:hover {
            z-index: 1;
            .border {
                background: $secondary-color;
            }
            .inner {
                opacity: 1;
            }
            .name {
                opacity: 0;
            }
            .logo {
                img {
                    opacity: 1;
                }
            }
        }
    }
}

