.video-section {
    position: relative;
    width: 100%;
    height: 100vh; 
    @include media(">=small-desktop") {
        height: 100%;
    }
    .video-holder {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        overflow: hidden;
        transition: 0.5s;
        transition-property: left, right;
        // @include media(">=tablet") {
        //     right: 25%;
        //     left: 25%;
        // }
        @include media(">=desktop") {
            right: 31.64%;
            left: 31.64%;
        }
    }
    video {
        position: absolute;
        top: 50%;
        left: 50%;
        min-width: 100%;
        min-height: 100%;
        transform: translate(-50%,-50%);
    }
    .mask {
        position: absolute;
        top: 0;
        right: -100%;
        bottom: 0;
        left: -100%;
        background: rgba($primary-color,0.6);
    }
    .info {
        position: absolute;
        left: 50%;
        bottom: 50%;
        width: 76%;
        max-width: 800px;
        transform: translate(-50%, 0);
        text-align: center;
        @include media(">=desktop") {
            width: 64%;
        }
        h1 {
            margin: 0 0 12px;
            @include media(">=desktop") {
                margin: 0 0 74px;
            }
        }
    }
    .video-play {
        position: absolute;
        bottom: -85px;
        left: 50%;
        border-radius: 50%;
        @include size(45px);
        background: $white;
        transform: translate(-50%, 0);
        @include media(">=desktop") {
            bottom: -35px;
        }
        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 50%;
            @include size(100px);
            border: 1px dashed rgba($white,0.5);
            transform: translate(-50%, -50%);
            transition: all 1s;
            @include media(">=desktop") {
                @include size(126px);
            }
        }
        &:after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            border: solid transparent;
            border-width: 4px 0 4px 7px;
            border-left-color: $secondary-color;
            @include size(0);
            transform: translate(-3px, -50%);
        }
        &:hover {
            &:before {
                @include size(80px);
                border-style: solid;
                @include media(">=desktop") {
                    @include size(100px);
                }
            }
        }
    }
}


.video-popup {
    display: none;
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    video {
        position: absolute;
        top: 50%;
        left: 50%;
        min-width: 100%;
        min-height: 100%;
        transform: translate(-50%,-50%);
    }
    .video-closer {
        position: absolute;
        z-index: 15;
        top: 15px;
        right: 15px;
        border-radius: 50%;
        @include size(40px);
        @include media(">=tablet") {
            top: 35px;
            right: 50px;
        }
        &:before,
        &:after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -1px 0 0 -15px;
            width: 30px;
            height: 2px;
            background: $white;
            transform: rotate(-45deg);
        }
        &:after {
            transform: rotate(45deg);
        }
    }
}