.partners-section {
    padding: 30px 15px;
    @include media(">=small-desktop") {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        padding: 0;
        width: 460px;
    }
    h2 {
        margin: 0 0 30px;
        line-height: 35px;
        font-size: 20px;
        font-weight: 700;
        color: $secondary-color;
        @include media(">=small-desktop") {
            margin: 0;
            line-height: 35px;
            font-size: 25px;
            white-space: nowrap;
            transform: rotate(-90deg);
            transform-origin: 100% 0;
            position: absolute;
            top: -5px;
            right: calc(100% + 112px);
        }
    }
}

.partners-table {
    width: 100%;
    margin: 0 auto;
    max-width: 460px;
    line-height: 0;
    font-size: 0;
    .partner {
        display: inline-block;
        vertical-align: top;
        position: relative;
        margin: -1px 0 0 -1px;
        width: calc(50% + 1px);
        height: 0;
        padding-top: 41%;
        @include media(">=small-desktop") {
            padding-top: 26%;
        }
        .border {
            position: absolute;
            // transition: background 0.25s;
        }
        .top,
        .bottom {
            top: 0;
            left: 0;
            right: 0;
            height: 1px;
            background: url("../images/white-dots.png");
        }
        .bottom {
            top: auto;
            bottom: 0;
        }
        .left,
        .right {
            top: 0;
            left: 0;
            bottom: 0;
            width: 1px;
            background: url("../images/white-dots.png");
        }
        .right {
            left: auto;
            right: 0;
        }
        .name {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            // transition: opacity 0.25s;
            span {
                display: block;
                text-align: center;
                line-height: 58px;
                font-size: 60px;
                font-weight: 700;
            }
            strong {
                display: block;
                line-height: 14px;
                font-size: 14px;
                font-weight: 400;
                text-align: center;
                color: rgba($white, 0.2)
            }
        }
        .logo {
            // opacity: 0;
            // transition: opacity 0.25s;
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                max-width: 80%;
                max-height: 80%;
                opacity: 0;
            }
        }
        &:hover {
            .border {
                background: $white;
            }
            .inner {
                opacity: 1;
            }
            .name {
                opacity: 0;
            }
            .logo {
                img {
                    opacity: 1;
                }
            }
        }
    }
}

