$fonts: '../fonts/';

@font-face {
  font-family: 'Sharp_Grotesk';
  src: url('#{$fonts}sharpgroteskdbbook20-webfont.woff2') format('woff2'),
       url('#{$fonts}sharpgroteskdbbook20-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}

@font-face {
  font-family: 'Sharp_Grotesk';
  src: url('#{$fonts}sharpgroteskdbsmbold20-webfont.woff2') format('woff2'),
       url('#{$fonts}sharpgroteskdbsmbold20-webfont.woff') format('woff');
  font-weight: bold;
  font-style: normal;

}

