@font-face {
  font-family: 'icomoon';
  src:  url('#{$fonts}icomoon.eot?j082sf');
  src:  url('#{$fonts}icomoon.eot?j082sf#iefix') format('embedded-opentype'),
    url('#{$fonts}icomoon.ttf?j082sf') format('truetype'),
    url('#{$fonts}icomoon.woff?j082sf') format('woff'),
    url('#{$fonts}icomoon.svg?j082sf#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow-left:before {
  content: "\e900";
}
.icon-arrow-right:before {
  content: "\e901";
}
.icon-facebook:before {
  content: "\e902";
}
.icon-linkedin:before {
  content: "\e904";
}
.icon-twitter:before {
  content: "\e905";
}
.icon-instagram:before {
  content: "\e903";
}
