.programs-block {
  margin: 0 auto 60px auto;

  @include media(">=desktop") {
    margin: 0 auto 100px auto;
  }

  .bottom-mr{
    @include media("<desktop") {
      margin-bottom: 60px;
    }
  }

  .programs-description{
    @include media(">=desktop") {
      margin-bottom: 102px;
    }
  }

  h6{
    font-weight: 400;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 8px;
  }

  h2 {
    color: $black;
    @include media(">=desktop") {
      margin-bottom: 42px;
    }
  }
  .programs-single{
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    p{
      opacity: 0.5;

      @include media(">=desktop") {
        max-width: 302px;
      }
    }

    h3{
      @include media(">=desktop") {
        font-size: 40px;
        line-height: 50px;
        margin-bottom: 22px;
      }
    }
  }
}
