.participants-block {
  margin: 0 auto 60px;

  @include media(">=desktop") {
    margin: 0 auto 114px;
    padding: 38px 0 35px;
  }
}

.participants-image {
  text-align: center;
  img {
    max-width: 100%;
    vertical-align: top;
  }
}

.participants-circle {

  @include media(">=desktop") {
    position: relative;
    width: 600px;
    height: 600px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }


  svg {
    display: none;
    @include media(">=desktop") {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
    }
  }

  .blue-circle {
    @include media(">=desktop") {
      display: block;
      width: 19px;
      height: 19px;
      border-radius: 100%;
      background-color: $primary-color;
      position: absolute;
      top: -9px;
      left: -9px;
    }
  }

  h4 {
    @include media(">=desktop") {
      margin: 0;
      max-width: 392px;
      text-align: center;
    }
  }

  .circle-list {

    @include media(">=desktop") {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      margin: 0;
    }

    p {
      @include media(">=desktop") {
        position: absolute;
        white-space: nowrap;
      }
    }

    li {
      @include media(">=desktop") {
        position: absolute;
        padding-left: 0;
        font-weight: 700;

        &:nth-child(2), &:nth-child(3), &:nth-child(4), &:nth-child(6), &:nth-child(7), &:nth-child(8) {
          p {
            top: -10px;
          }
        }

        &:nth-child(2), &:nth-child(3), &:nth-child(4) {
          p {
            left: 25px;
          }
        }

        &:nth-child(6), &:nth-child(7), &:nth-child(8) {
          p {
            right: 17px;
          }
        }

        &:before {
          display: none;
        }

        &:nth-child(1) {
          top: 0;
          left: 50%;
          p {
            top: -40px;
            transform: translateX(-50%);
          }
        }

        &:nth-child(2) {
          top: 100px;
          right: 77px;
        }

        &:nth-child(3) {
          top: 50%;
          right: 0;
        }

        &:nth-child(4) {
          top: 500px;
          right: 77px;
        }

        &:nth-child(5) {
          bottom: 0;
          left: 50%;
          p {
            transform: translateX(-50%);
            bottom: -65px;
          }
        }

        &:nth-child(6) {
          top: 500px;
          left: 77px;
          p {
            right: 26px;
          }
        }

        &:nth-child(7) {
          top: 50%;
          left: 0;
        }

        &:nth-child(8) {
          top: 100px;
          left: 77px;
        }
      }
    }
  }
}