.intro-block {
  background-color: $primary-color;
  @extend %full-width;
  color: $white;
  margin-bottom: 50px;
  @include media(">=desktop") {
    margin-bottom: 104px;
  }
  .intro-content {
    padding: 110px 0 20px;
    @include media(">=tablet") {
      padding: 165px 0 85px;
    }
    @include media(">=desktop") {
      padding: 163px 0 76px;
    }
  }
  .intro-titles {
    max-width: 550px;
  }
  a {
    height: 30px;
    width: 30px;
    display: block;
    margin: 0 auto 20px;
    background: {
      image: url('../images/icon-arrow-left-white.svg');
      repeat: no-repeat;
      size: 17px;
      position: center center;
    };
    @include media(">=tablet") {
      margin: 0 auto 51px;
    }
  }
  h1 {
    margin-bottom: 0;
    color: inherit;
  }
  span {
    color: inherit;
    text-transform: uppercase;
    display: block;
    @include media(">=tablet") {
      margin-bottom: 16px;
    }
  }
}