.footer {
    display: none;
    padding: 20px 50px 32px;
    line-height: 14px;
    font-size: 12px;
    @include media(">=small-desktop") {
        display: block;
    }
    .inverse & {
        position: absolute;
        z-index: 3;
        right: 150px;
        bottom: 0;
        left: 0;
    }
    .menu & {
        display: block;
        position: relative;
        right: auto;
        bottom: auto;
        left: auto;
        padding: 12px 0 0;
        width: 100%;
        @include media(">=small-desktop") {
            display: none;
        }
    }
    p {
        margin: 0;
        opacity: 0.3;
    }
}

.bottom-nav {
    margin: 0 0 28px;
    .menu & {
        margin: 0 0 10px;
    }
    li {
        @extend %reset-list-bullets;
        display: inline;
        margin: 0 18px 0 0;
        .menu & {
            display: block;
            margin: 0;
            padding: 4px 0 14px;
        }
    }
    a {
        color: $secondary-color;
    }
}