.initiative-section {
    padding: 30px 15px 0;
    @include media(">=small-desktop") {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 80%;
        // padding: 0 0 0 8%;
    }
    @include media(">=desktop") {
        width: 70%;
        // width: 60%;
        // padding: 0 0 0 8%;
    }
    .info {
        margin: 0 0 45px;
        @include media(">=small-desktop") {
            margin: 0;
            width: calc(100% - 280px);
            // max-width: 500px;
            max-width: 600px;
        }
    }
    .title {
        display: block;
        line-height: 35px;
        font-size: 20px;
        font-weight: 700;
        color: $secondary-color;
        @include media(">=small-desktop") {
            line-height: 35px;
            font-size: 25px;
        }
    }
    .marked {
        color: $white;
    }
    .button-holder {
        text-align: center;
    }
}