.header {
    position: absolute;
    z-index: 6;
    top: 0;
    right: 0;
    left: 0;
    @include media(">=tablet") {
        position: fixed;
    }
}

.main-logo {
    position: absolute;
    top: 15px;
    left: 15px;
    @include media(">=tablet") {
        top: 50px;
        left: 50px;
    }
    .color {
        fill: $white;
        transition: fill 0.25s;
        .inverted & {
            fill: $primary-color;
        }
    }
    .desktop {
        display: none;
        @include media(">=tablet") {
            display: block;
        }
    }
    .mobile {
        display: block;
        @include media(">=tablet") {
            display: none;
        }
    }
}


.socials-nav {
    position: absolute;
    top: 24px;
    right: 90px;
    @include media(">=tablet") {
        top: 47px;
        right: 146px;
    }
    li {
        float: left;
        margin: 0 0 0 9px;
        @extend %reset-list-bullets;
    }
    a {
        color: $white;
        text-decoration: none;
        transition: opacity 0.25s;
        .inverted & {
            color: $primary-color;
        }
        &:hover {
            opacity: 0.5;
        }
    }
    span {
        display: none;
    }
}



.menu-opener {
    position: absolute;
    z-index: 10;
    top: 15px;
    right: 15px;
    @include size(40px, 40px);
    line-height: 0;
    font-size: 0;
    @include media(">=tablet") {
        position: fixed;
        top: 35px;
        right: 50px;
    }
    .t,
    .b {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        margin: -1px 0 0 -13px;
        @include size(27px, 2px);
        background: $white;
        transform: translate(-7px,-4px);
        transition: transform 0.25s;

    }
    .b {
        transform: translate(6px,5px);
    }
    .menu-opened & {
        .t,
        .b {
            transform: translate(0);
            background: $white;
        }
    }
}
.inverted ~ .menu-opener {
    .t,
    .b {
        background: $primary-color;
        .menu-opened & {
            background: $white;
        }
    }
}



.menu-opened {
    @include media("<tablet") {
        position: relative;
        overflow: hidden;
        height: 100%;
    }
}

.menu {
    display: flex;
    flex-direction: column;
    justify-content:space-between;
    align-items: center;
    position: absolute;
    z-index: 6;
    top: 0;
    right: 0;
    left: 0;
    height: 100vh;
    overflow: auto;
    padding: 74px 15px 12px;
    color: $white;
    background: $primary-color;
    opacity: 0;
    transition: opacity 1s;
    pointer-events: none;
    @include media(">=tablet") {
        position: fixed;
        bottom: 0;
        height: auto;
    }
    @include media(">=small-desktop") {
        justify-content: center;
        padding: 20px;
    }
    .menu-opened & {
        opacity: 1;
        pointer-events: auto;
    }
    .decor {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        opacity: 0.1;
    }
    >ul {
        position: relative;
        margin: 0 0 42px;
        text-align: center;
        @include media(">=tablet") {
            margin: 0;
        }
    }
    >ul>li {
        @extend %reset-list-bullets;
        padding: 23px 0;
        line-height: 24px;
        font-size: 20px;
        font-weight: bold;
        @include media(">=tablet") {
            padding: 38px 0;
            line-height: 35px;
            font-size: 25px;
        }

    }
    >ul>li>a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        text-decoration: none;
        color: $white;
        &:before {
            content: "";
            position: absolute;
            bottom: -5px;
            left: 51%;
            right: 51%;
            height: 3px;
            background: $white;
            transition: 0.25s linear;
            transition-property: left, right;
        }
        &:hover {
            &:before {
                left: 0;
                right: 0;
            }
            
        }
    }
}