// variables

// colors
$primary-color: #7b00ff;
$secondary-color: #16161D;

$white: #fff;
$light-gray: #ccc;
$medium-gray: #bbb;
$dark-gray: #aaa;
$black: #000;
$danger-color: red;
$warning-color: yellow;
$success-color: green;

$main-fz: 14px;
$main-lh: 1.715;
$main-text-color: $secondary-color;
$main-ff: "Sharp_Grotesk", "Arial", "Helvetica", sans-serif;
$headings-lh: 1.2;
$main-margin: $main-fz * $main-lh;

// grid settings
$gl-gridName: grid;
$gl-colName: col;
$gl-attributeName: class;
$gl-gutter: 30px; // Total left + right
$gl-gutter-vertical: 0;
$gl-mq-width: 'min-width';
$gl-mq-list: (
    xs: 320px,
    sm: 768px,
    md: 1200px,
    lg: 1600px
);

// media settings
$breakpoints: (tablet: 768px, small-desktop: 1025px, desktop: 1200px);

body {
    font: #{$main-fz}/#{$main-lh} $main-ff;
    color: $main-text-color;
    background: $white;
    @include media("<tablet") {
        height: auto;
    }
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
    color: $primary-color;
    .inverse & {
        color: $white;
    }
}

h1,
.h1 {
    margin-bottom: 20px;
    line-height: 40px;
    font-size: 30px;
    @include media(">=tablet") {
        margin-bottom: 70px;
        line-height: 70px;
        font-size: 60px;
    }
}

h2,
.h2 {
    margin-bottom: 20px;
    line-height: 32px;
    font-size: 26px;
    @include media(">=tablet") {
        margin-bottom: 50px;
        line-height: 50px;
        font-size: 40px;
    }
}

h3,
.h3 {
    margin-bottom: 20px;
    line-height: 30px;
    font-size: 24px;
    @include media(">=tablet") {
        margin-bottom: 40px;
        line-height: 40px;
        font-size: 30px;
    }
}

h4,
.h4 {
    margin-bottom: 20px;
    line-height: 30px;
    font-size: 22px;
    @include media(">=tablet") {
        margin-bottom: 35px;
        line-height: 35px;
        font-size: 25px;
    }
}

h5,
.h5 {
    margin-bottom: 28px;
    line-height: 28px;
    font-size: 18px;
    @include media(">=tablet") {
        margin-bottom: 20px;
        line-height: 26px;
        font-size: 20px;
    }
}

h6,
.h6 {
    margin-bottom: 24px;
    line-height: 24px;
    font-size: 14px;
}

p {
    margin-bottom: $main-margin;
}

a {
    text-decoration: underline;
    color: inherit;
    outline: none;
    &:hover {
        text-decoration: none;
    }
}

ul {
    margin-bottom: $main-margin;
    li {
        padding-left: 25px;
        &:before {
            content: "";
            float: left;
            margin: 12px 0 0 -25px;
            border-radius: 50%;
            width: 2px;
            height: 2px;
            background: $secondary-color;
        }
    }
}

ol {
    margin-bottom: $main-margin;
    counter-reset: counterName;
    li {
        &:before {
        counter-increment: counterName;
        content: counters(counterName, "-") ". ";
        }

    }
    ul,
    ol {
        margin: 0;
        padding-left: 25px;
    }
}

// forms

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="number"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    border: 1px solid transparent;
    padding: 9px 24px;
    line-height: 60px;
    font-size: 14px;
    font-family: $main-ff;
    height: 80px;
    vertical-align: middle;
    color: $secondary-color;
    background: $white;
    outline: none;
    transition: border-color 0.3s;
    -webkit-appearance: none;
    border-radius: 0;
    @include placeholder {
        color: rgba($secondary-color, 0.4);
    }
    &:focus {
        border-color: $primary-color;
    }
}

.button,
input[type="submit"],
button {
    display: inline-block;
    margin: 0 0 $main-margin;
    border: 1px solid transparent;
    padding: 9px 24px;
    min-width: 150px;
    height: 50px;
    line-height: 30px;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    white-space: nowrap;
    color: $white;
    background-color: $primary-color;
    transition: 0.3s;
    transition-property: color, background-color;
    cursor: pointer;
    -webkit-appearance: none;
    border-radius: 0;
    @include media(">=tablet") {
        line-height: 60px;
        height: 80px;
    }
    &:hover,
    &:focus {
        color: $white;
        background-color: darken($primary-color, 10%);
    }
    &.secondary {
        background-color: $secondary-color;
        &:hover,
        &:focus {
            background-color: darken($secondary-color, 10%);
        }
    }
    &.arrow {
        padding-right: 66px;
        background-image: url('../images/icon-arrow-right-white.svg');
        background-repeat: no-repeat;
        background-position: right 24px center;
    }
    &.danger {
        background-color: $danger-color;
        &:hover,
        &:focus {
            background-color: darken($danger-color, 10%);
        }
    }
    &.warning {
        background-color: $warning-color;
        &:hover,
        &:focus {
            background-color: darken($warning-color, 10%);
        }
    }
    &.success {
        background-color: $success-color;
        &:hover,
        &:focus {
            background-color: darken($success-color, 10%);
        }
    }
    &.animated {
        position: relative;
        line-height: 60px;
        height: 80px;
        background-color: transparent;
        span {
            position: relative;
        }
        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 30%;
            background: rgba($white, 0.2);
            transition: all 0.25s;
        }
        &:hover {
            &:before {
                left: 0;
            }
        }
    }
    &.large {
        font-size: 26px;
        font-weight: 400;
        text-transform: none;
        &:before {
            left: 0;
        }
        &:hover {
            &:before {
                background: rgba($white, 0.5);
            }
        }
    }
}

input[type="checkbox"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

input[type="radio"] {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

label {
    display: inline-block;
    margin: 0 0 $main-margin;
    vertical-align: middle;
}

blockquote {
    position: relative;
    margin: 0 0 $main-margin;
    min-height: 50px;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -20px;
        left: -6px;
        width: 85px;
        height: 65px;
        background: url('../images/icon-quote.svg');
    }
    p {
        margin: 0;
    }
    q {
        quotes: none;
        &:before,
        &:after {
            quotes: none;
        }
    }
    cite {
        display: block;
        text-align: right;
        &:before {
            content: "-";
            margin-right: 5px;
        }
    }
}

table {
    margin: 0 0 $main-margin;
    th,
    td {
        border: 1px solid $medium-gray;
        padding: 5px;
    }
}
