.goals-section {
    padding: 30px 15px;
    @include media(">=small-desktop") {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 80%;
        padding: 0 0 0 8%;
    }
    @include media(">=desktop") {
        width: 91.3%;
        padding: 0 0 0 6.7%;
    }
    @include media(">=1600px") {
        width: 80%;
        padding: 0 0 0 8%;
    }
}


.quote-box {
    margin: 0 0 60px;
    @include media(">=small-desktop") {
        margin: 0;
        width: 32%;
        max-width: 300px;
    }
    @include media(">=desktop") {
        width: 28%;
        padding-bottom: 130px;
    }
    .title {
        display: block;
        margin: 0 0 35px;
        line-height: 30px;
        font-size: 20px;
        font-weight: 700;
        color: $secondary-color;
        @include media(">=small-desktop") {
            margin: 0 0 45px;
            line-height: 35px;
            font-size: 25px;
        }
    }
    blockquote {
        &:before {
            background: url('../images/icon-quotes.svg');
            opacity: 0.7;
        }
    }
}




.goals-table-holder {
    margin: 0 auto;
    max-width: 750px;
    @include media(">=small-desktop") {
        margin: 0;
        width: 60%;
        max-width: 750px;
    }
    @include media(">=desktop") {
        width: 68.8%;
    }
    @include media(">=1921px") {
        max-width: 1000px;
    }
    @include media(">=tablet", "height<800px") {
        max-width: 600px;
    }
}
.goals-table {
    // display: flex;
    // flex-wrap: wrap;
    // border-left: 1px solid $white;
    // border-top: 1px solid transparent;
    line-height: 0;
    font-size: 0;
    .cell {
        display: inline-block;
        vertical-align: top;
        position: relative;
        margin: -1px 0 0 -1px;
        // border: 1px solid $white;
        padding-top: 50%;
        width: calc(50% + 1px);
        height: 0;
        @include media(">=480px") {
            padding-top: 33%;
            width: calc(33% + 1px);
        }
        @include media(">=767px") {
            padding-top: 25%;
            width: calc(25% + 1px);
        }
        @include media(">=small-desktop") {
            padding-top: 20%;
            width: calc(20% + 1px);
        }
        .border {
            position: absolute;
            // transition: background 0.25s;
        }
        .top,
        .bottom {
            top: 0;
            left: 0;
            right: 0;
            height: 1px;
            background: url("../images/white-dots.png");
        }
        .bottom {
            top: auto;
            bottom: 0;
        }
        .left,
        .right {
            top: 0;
            left: 0;
            bottom: 0;
            width: 1px;
            background: url("../images/white-dots.png");
        }
        .right {
            left: auto;
            right: 0;
        }
        .inside {
            position: absolute;
            top: 50%;
            left: 50%;
            line-height: 60px;
            font-size: 60px;
            font-weight: 700;
            transform: translate(-50%,-50%);
            @include media(">=small-desktop") {
                line-height: 50px;
                font-size: 40px;
            }
            @include media(">=desktop") {
                line-height: 60px;
                font-size: 60px;
            }
        }
        .inner {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            opacity: 0;
            :not(.touch) & {
                transition: opacity 0.25s;
            }
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                max-width: 80%;
                max-height: 80%;
                @include media(">=desktop") {
                    max-width: calc(100% - 40px);
                    max-height: calc(100% - 40px);
                }
            }
        }
        &:hover {
            .border {
                background: $white;
            }
            .inner {
                opacity: 1;
            }
        }
    }
}
