.award-block{
  margin: 0 auto 60px auto;

  @include media(">=desktop") {
    margin: 0 auto 100px auto;
    max-width: 970px;
  }
  
  .award-image-holder{
    text-align: center;
  }
  .icons {
    text-align: center;
  }
  .icon {
    margin: 0 auto 20px;
    padding: 20px 10px;
    width: 140px;
    min-height: 140px;
    text-align: center;
    color: $white;
    background: $primary-color;
    @include media(">=568px") {
      display: inline-block;
      margin: 0 10px 20px;
      vertical-align: top;
    }
    @include media(">=desktop") {
      display: block;
      margin: 0 auto 20px;
    }
    img {
      display: block;
      margin: 0 auto 10px;
      max-width: 40px;
      max-height: 40px;
    }
    .title {
      display: block;
      border-top: 1px solid $white;
      padding: 10px 0 0;
      line-height: 16px;
      font-size: 14px;
    }
  }

  h2{
    color: $black;
  }

  li{
    &:before{
      width: 3px;
      height: 3px;
      background: $primary-color;
    }
  }
}
.award-description {
  padding: 0 0 0 24px;
  ul {
    margin-left: -24px;
  }
}