

.subscribe-form {
    margin: 0 auto 50px;
    max-width: 970px;
    background: $white;
    @include media(">=small-desktop") {
        margin: 0 auto 110px;
        padding: 0 0 0 42px;
        background: #f7f7f7;
    }
    .involve-holder & {
        margin: 0;
        max-width: initial;
        transition: transform 0.25s;
        transform: translate(0, 100%);
        @include media(">=small-desktop") {
            padding: 0 0 0 30px;
            width: 620px;
            transform: translate(-100%, 0);
        }
    }
    .involve-holder.subscribe-opened & {
        transform: translate(0, 0);
    }
    form {
        text-align: center;
        @include media(">=small-desktop") {
            display: flex;
            justify-content: space-between;
            align-items: center;
            text-align: left;
        }
    }
    h3 {
        margin: 0 0 2px;
        line-height: 24px;
        font-size: 20px;
        font-weight: bold;
        color: $secondary-color;
        @include media(">=small-desktop") {
            margin: 0;
            width: 162px;
            line-height: 30px;
            font-size: 20px;
        }
        .involve-holder & {
            @include media(">=small-desktop") {
                position: absolute;
                top: 16px;
                left: 30px;
            }
        }
    }
    label {
        display: block;
        margin: 0 0 30px;
        line-height: 20px;
        font-size: 14px;
        color: $secondary-color;
        @include media(">=small-desktop") {
            margin: 0;
            padding: 0 10px 0 0;
            width: calc(49.6% - 162px);
        }
        .involve-holder & {
            @include media(">=small-desktop") {
                padding: 30px 10px 0 0;
                width: 237px;
            }
        }
    }
    input[type="email"] {
        display: block;
        margin: 0 auto 40px;
        border: 0;
        border-left: 2px solid $primary-color;
        padding: 0 35px;
        width: 260px;
        height: 27px;
        line-height: 27px;
        font-size: 14px;
        background: none;
        @include placeholder {
            color: rgba($secondary-color, 0.4);
        }
        @include media(">=small-desktop") {
            margin: 0;
            width: calc(50.5% - 150px);
        }
        .involve-holder & {
            @include media(">=small-desktop") {
                width: calc(100% - 237px - 150px);
            }
        }
    }
    input[type="submit"] {
        display: block;
        margin: 0 auto;
        width: 150px;
        height: 50px;
        line-height: 30px;
        background: $secondary-color;
        @include media(">=small-desktop") {
            margin: 0;
            height: 80px;
            line-height: 60px;
        }
        &:hover {
            background: $primary-color;
        }
    }
}

.involve-holder {
    position: fixed;
    z-index: 4;
    bottom: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
    @include media(">=small-desktop") {
        position: fixed;
        bottom: 140px;
        width: auto;
    }
    &.subscribe-opened {
        pointer-events: all;
    }
}

.subscribe-opener {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 20px;
    text-align: center;
    width: 150px;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    background: rgba($white, 0.2);
    opacity: 1;
    pointer-events: all;
    transition: 0.25s;
    transition-property: background, opacity;
    @include media(">=small-desktop") {
        height: 80px;
        line-height: 80px;
    }
    &:hover {
        background: rgba($white, 0.5);
    }
    .subscribe-opened & {
        opacity: 0;
    }
}

.subscribe-closer {
    display: none;
    margin: 22px 0;
    color: $primary-color;
    text-decoration: none;
    i {
        display: block;
        transform: rotate(90deg);
    }
    .involve-holder & {
        @include media("<small-desktop") {
            display: inline-block;
        }
    }
}
