
.not-found {
    position: relative;
    z-index: 2;
    padding: 15px 10px;
    height: 100vh;
    min-height: 480px;
    @include media(">=tablet") {
        padding: 50px 20px;
    }
    .logo {
        display: block;
        margin: 0 auto;
        width: 245px;
        height: 55px;
    }
    .info {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 300px;
        text-align: center;
        transform: translate(-50%,-50%);
    }
    h1 {
        margin: 0 0 5px;
        color: $white;
        @include media(">=tablet") {
            margin: 0 0 15px;
        }
    }
    h2 {
        margin: 0 0 10px;
        line-height: 30px;
        font-size: 20px;
        color: $secondary-color;
        @include media(">=tablet") {
            margin: 0 0 45px;
            line-height: 35px;
            font-size: 25px;
        }
    }
    .button {
        background: rgba($white, 0.2);
        &:hover {
            background: rgba($white, 0.5);
        }
    }
}