.values-block{
  margin: 0 auto 60px;
  @include media(">=desktop") {
    margin: 0 auto 100px auto;
  }

  h2{
    color: $black;

    @include media(">=desktop") {
      margin-bottom: 100px;
    }
  }
}
.values-table{
  text-align: center;

  ul{
    margin-top: 30px;
    text-align: left;
    color: $primary-color;
    margin-bottom: 0;
  }

  li{
    &:before{
      background: $primary-color;
    }
  }

  p{
    margin: 0;
    opacity: 0.5;
    transition: opacity .6s ease;
  }

  h6, .big-letter{
    color: $black;
    text-transform: uppercase;
  }

  h6{
    transition: color .6s ease;
  }

  .big-letter{
    color: $primary-color;
    font-family: $main-ff;
    font-size: 60px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 15px;
  }

  .dots-border{
    position: absolute;
    transition: background-color .6s ease;
  }

  .top, .bottom{
    height:1px;
    width: 100%;
    left: 0;
    background: url("../images/black-dots.png");
  }

  .left, .right{
    height: 100%;
    width: 1px;
    top: 0;
    background: url("../images/black-dots.png");
  }

  .top{
    top: -0.5px;
  }

  .bottom{
    bottom: -0.5px;
  }

  .left{
    left: -0.5px;
  }
  .right{
    right: -0.5px;
  }

  .values-item{
    padding: 50px 20px;
    position: relative;
    transition: border-color .4s ease;
    display: flex;
    // align-items: center;
    justify-content: center;
    @include media(">=tablet") {
      padding: 75px 95px;
    }

    &:hover{

      p{
        opacity: 1;
      }

      h6{
        color: $primary-color;
      }

      .dots-border{
        background-color: black;
      }
    }
  }
}