.info-block {
  .info-content {
    margin-bottom: 45px;
    @include media('>=desktop') {
      margin-bottom: 74px;
    }
    &.with-line {
      position: relative;
      padding-bottom: 15px;
      @include media('>=desktop') {
        padding-bottom: 30px;
      }
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        width: 100%;
        height: 2px;
        background-color: #f7f7f7;
      }
    }
  }
  .marked {
    color: $primary-color;
  }
  .button {
    margin-bottom: 5px;
  }
  .info-title {
    font-weight: bold;
    margin-bottom: 30px;
    @include media('>=desktop') {
      margin-bottom: 63px;
    }
  }
  h2, .h2 {
    color: $black;
    @include media('>=desktop') {
      margin-bottom: 43px;
    }
  }
  .info-text {
    .h4, h4 {
      font-weight: bold;
      strong {
        color: $primary-color;
      }
    }
  }
  .info-post {
    align-self: center;
    img {
      width: 100%;
      margin-bottom: 22px;
      vertical-align: top;
    }
    .h4, h4 {
      font-weight: bold;
      @include media('>=desktop') {
        margin-bottom: 50px;
      }
    }
  }
  .m-auto {
    @include media('>=tablet','<desktop') {
      margin: 0 auto;
    }
  }
  ol {
    margin-bottom: 20px;
    line-height: 30px;
    font-size: 22px;
    font-weight: 700;
    @include media(">=tablet") {
        margin-bottom: 35px;
        line-height: 35px;
        font-size: 25px;
    }
    li {
      padding-bottom: 15px;
    }
  }
}