
.awards-section {
    padding: 30px 15px;
    @include media(">=small-desktop") {
        position: absolute;
        top: 50%;
        left: 50%;
        padding: 0 0 0 8%;
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        transform: translate(-50%,-50%);
    }
    @include media(">=desktop") {
        padding: 0 0 0 10%;
        width: 80.2%;
    }
    .info {
        @include media(">=small-desktop") {
            width: 40%;
            max-width: 400px;
        }
        .author {
            display: block;
            margin: 0 0 6px;
            font-weight: 400;
            text-transform: uppercase;
            @include media(">=small-desktop") {
                margin: 0 0 16px;
            }
        }
        h2 {
            margin: 0 0 15px;
            line-height: 40px;
            font-size: 30px;
            @include media(">=small-desktop") {
                margin: 0 0 20px;
                line-height: 50px;
                font-size: 40px;
            }
            @include media(">=desktop") {
                line-height: 70px;
                font-size: 60px;
            }
            @include media(">=tablet", "height<800px") {
                line-height: 50px;
                font-size: 40px;
            }
        }
    }
    .button-holder {
        margin: 0 0 25px;
        text-align: center;
        @include media(">=small-desktop") {
            margin: 0;
        }
    }
    .button {
        @include media(">=small-desktop") {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-10%,-50%);
        }
    }
    .aside-list {
        position: relative;
        @include media(">=small-desktop") {
            width: 21.8%;
        }
        h3 {
            position: absolute;
            top: 3px;
            right: calc(100% + 76px);
            margin: 0;
            line-height: 14px;
            font-size: 14px;
            font-weight: 400;
            white-space: nowrap;
            text-transform: uppercase;
            transform: rotate(-90deg);
            transform-origin: 100% 0;
            @include media(">=small-desktop") {

            }
        }
        .inside {
            @include media(">=tablet", "height<800px") {
                max-height: 400px;
            }
            @include media(">=small-desktop") {
                margin: 0 -70px 0 -50px;
            }
        }
    }
}


.values-list {
    margin: 0;
    padding: 0 45px;
    line-height: 24px;
    font-size: 14px;
    counter-reset: themesCounter;
    @include media(">=small-desktop") {
        margin: 0 20px 0 0;
        padding: 0 0 0 50px;
    }
    li {
        position: relative;
        margin: 0 0 20px 0;
        padding: 0 0 20px 25px;
        @include media(">=desktop") {
            margin: 0 0 30px;
            padding: 0 0 30px 25px;
        }
        &:before {
            position: absolute;
            top: -3px;
            left: 0;
            line-height: 25px;
            font-size: 25px;
            font-weight: 700;
            color: $secondary-color;
            counter-increment: themesCounter;
            content: counters(themesCounter, "-") "";
            @include media(">=small-desktop") {
                transition: transform 0.25s;
            }
        }
        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            @include size(72px,1px);
            background: $secondary-color;
        }
        &:last-child {
            &:after {
                display: none;
            }
        }
        .title {
            display: block;
            text-transform: uppercase;
        }
        p {
            margin: 0 0 0 -25px;
            color: $white;
            @include media(">=small-desktop") {
                color: rgba($white, 0.2);
            }
        }
        .animated {
            @include media(">=small-desktop") {
                transition: transform 0.25s;
            }
        }
        &:hover {
            &:before {
                @include media(">=small-desktop") {
                    transform: translateX(-30px);
                }
            }
            .animated {
                @include media(">=small-desktop") {
                    transform: translateX(-30px);
                }
            }
            p {
                color: $white;
            }
        }
    }
}